// Misc.
$misc: (
	z-index-base: 10000,
);

// Duration.
$duration: (
	menu: 0.5s,
	transition: 0.2s,
);

// Size.
$size: (
	element-height: 2.75em,
	element-margin: 2em,
	section-spacing: 3em,
	section-spacing-small: 1.5em,
	menu: 25em,
);

// Font.
$font: (
	family: (
		"Open Sans",
		Helvetica,
		sans-serif,
	),
	family-fixed: (
		"Courier New",
		monospace,
	),
	family-heading: (
		"Open Sans",
		Helvetica,
		sans-serif,
	),
	weight: 200,
	weight-bold: 350,
	weight-heading: 200,
	weight-heading-bold: 400,
	weight-heading-extrabold: 450,
	kerning-heading: 0.25em,
);

// Palette.
$palette: (
	bg: #1f1f1f,
	bg-alt: #1b1b1b,
	fg: #bebebe,
	fg-bold: #c7c7c7,
	fg-light: #aaaaaa,
	border: rgba(44, 44, 44, 0.3),
	border-bg: rgba(160, 160, 160, 0.075),
	border-alt: rgba(160, 160, 160, 0.65),
	accent: #ba6d2e,
);
